.button {
    border: 1px solid #d0d0d0;
    color: #111;
    background: #fff;
    cursor: pointer;
    font-size: 12px;
    min-width: 28px;
    height: 28px;
    padding: 0 8px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    border-radius: 0; /* no rounded corners */
}
.button:hover {
    background: #f6f7f9;
}
.button:active {
    background: #eef0f3;
}

.editor-input {
    background-color: white;
    color: black;
    outline: none;
}

.dropdown {
    padding: 0 8px;
    height: 28px;
    border: 1px solid #d0d0d0;
    color: #111;
    background: #fff;
    display: inline-flex;
    align-items: center;
    gap: 6px;
    min-width: 110px;
    justify-content: space-between;
    cursor: pointer;
    border-radius: 0; /* no rounded corners */
    font-size: 11px; /* reduced */
}
.dropdown:hover {
    background: #f6f7f9;
}

.font-size-input-container {
    width: 44px;
    height: 28px;
    border: 1px solid #d0d0d0;
    border-radius: 0; /* no rounded corners */
    display: flex;
    align-items: center;
    background-color: white;
    color: black;
    outline: none;
    user-select: none;
    position: relative;
    overflow: hidden;
    font-size: 12px;
    font-family: monospace;
}

/* Remove prior icon-specific sizing */
.button.icon { all: unset; } 